import { Icon } from "@iconify/react";
import { Link } from "react-router-dom";
import "./styles/SearchResults.css";
import "./styles/SearchResults-mobile.css";
import { connect } from "react-redux";
import { invert } from '../../helpers';

function SearchResults({ results, setQuerySearch, theme }) {
  const onSearch = () => {
    setQuerySearch("");
  };

  return (
    <aside className={`_search_results bg-${theme} bb-${theme}`}>
      {results.map((result, i) => (
        <Link
          to={`/${result.nick}`}
          className="_search_result"
          key={i}
          onClick={onSearch}
        >
          <img src={result.avatar} alt={result.nick} />
          <div className="_search_nick_verified">
            <span className={`_search_user c3-${invert(theme)}`}>
              {result.nick}
              {result.verified && (
                <Icon
                  icon="codicon:verified-filled"
                  className="_verified_user_notify"
                />
              )}
            </span>
            <span className={`_search_name c2-${theme}`}>{result.user}</span>
          </div>
        </Link>
      ))}
    </aside>
  );
}

const mapStateToProps = (state) => ({
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(SearchResults);
