import { Icon } from "@iconify/react";
import EmojiPicker from "emoji-picker-react";
import { useEffect } from "react";
import { useState } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
import { getUser } from "../../mocks/fakeUsers";
import Message from "../Message";
import "./styles/DirectChat.css";
import "./styles/DirectChat-mobile.css";
import {
  excludeMessagesFromEveryone,
  excludeMessagesFromRecipient,
  excludeMessagesFromSender,
  getAllMessagesInChat,
  sendMessage,
} from "../../helpers/managerChats";
import { filterExcludeMessages, invert } from "../../helpers";

function DirectChat({ nickLogged, isMain, updateSenders, theme }) {
  const params = useParams();
  const senderUser = "user" in params ? getUser(params.user) : "";
  const loggedUser = getUser(nickLogged);
  const [message, setMessage] = useState("");
  const [emojiPicker, setEmojiPicker] = useState(false);
  const [openDelete, setOpenDelete] = useState(false);
  const [chats, setChats] = useState([]);
  const [selectedMessages, setSelectedMessages] = useState([]);

  const selectMessage = (sender, recipient, id) => {
    const alreadySelect = selectedMessages.some(
      (message) =>
        message.sender.nick === sender.nick &&
        message.recipient.nick === recipient.nick &&
        message.id === id
    );
    if (alreadySelect) {
      const newSelect = selectedMessages.filter((message) => {
        return !(
          message.sender.nick === sender.nick &&
          message.recipient.nick === recipient.nick &&
          message.id === id
        );
      });
      setSelectedMessages(newSelect);
    } else {
      setSelectedMessages([...selectedMessages, { sender, recipient, id }]);
    }
  };

  const deleteForMeSelectedMessages = () => {
    const senderMessages = selectedMessages.filter(
      (message) => message.sender.nick === nickLogged
    );
    const recipientMessages = selectedMessages.filter(
      (message) => message.sender.nick !== nickLogged
    );
    excludeMessagesFromSender(nickLogged, senderUser.nick, senderMessages);
    excludeMessagesFromRecipient(
      nickLogged,
      senderUser.nick,
      recipientMessages
    );
    setChats(getAllMessagesInChat(nickLogged, senderUser.nick));
    setSelectedMessages([]);
    handleDelete();
    updateSenders();
  };

  const deleteForEveryoneSelectedMessages = () => {
    const senderMessages = selectedMessages.filter(
      (message) => message.sender.nick === nickLogged
    );
    excludeMessagesFromEveryone(nickLogged, senderUser.nick, senderMessages);
    setChats(getAllMessagesInChat(nickLogged, senderUser.nick));
    setSelectedMessages([]);
    handleDelete();
    updateSenders();
  };

  const handleDelete = () => {
    setOpenDelete(!openDelete);
  };

  const closeHeaderMenu = () => {
    setSelectedMessages([]);
  };

  const scrollBottom = () => {
    setTimeout(() => {
      const chatsDiv = document.getElementById("chats");
      if (chatsDiv) {
        chatsDiv.scrollTop = chatsDiv.scrollHeight;
      }
      window.scrollTo(0, window.screen.height);
    }, 1);
  };

  useEffect(() => {
    const getChats = () => {
      setChats(getAllMessagesInChat(nickLogged, senderUser.nick));
    };
    const clearSelected = () => {
      setSelectedMessages([]);
    };
    clearSelected();
    getChats();
    scrollBottom();
  }, [nickLogged, params, senderUser.nick]);

  const handleChange = ({ target }) => {
    const { value } = target;
    setMessage(value);
  };

  const openCloseEmojiPicker = () => {
    setEmojiPicker(!emojiPicker);
  };

  const addEmoji = (emoji) => {
    setMessage(`${message}${emoji.emoji}`);
  };

  const checkSend = () => {
    return message.length <= 0;
  };

  const handleSendMessage = (e) => {
    e.preventDefault();
    sendMessage(nickLogged, senderUser.nick, message);
    setChats(getAllMessagesInChat(nickLogged, senderUser.nick));
    setMessage("");
    scrollBottom();
    updateSenders();
  };

  const disabledDeleteForAll = () => {
    return selectedMessages.every(
      (message) => message.sender.nick !== nickLogged
    );
  };

  return (
    <div
      className={`_direct_chat${
        isMain ? " _main" : ""
      } bg-${theme} bb-${theme}`}
    >
      {openDelete && (
        <div className="_delete_messages_menu">
          <div className={`_delete_messages_area bg-${theme} bb-${theme}`}>
            <div className={`_delete_warning bbB-${theme}`}>
              <span>
                {selectedMessages.length > 1
                  ? "Delete messages?"
                  : "Delete message?"}
              </span>
              <p className={`c-${invert(theme)}`}>
                If you delete the message just for you, the recipient will still
                be able to see the message!
              </p>
            </div>
            <section className="_delete_messages_buttons">
              <button
                className="_btn_delete _everyone"
                onClick={deleteForEveryoneSelectedMessages}
                disabled={disabledDeleteForAll()}
              >
                Delete for everyone
              </button>
              <button
                className="_btn_delete _me"
                onClick={deleteForMeSelectedMessages}
              >
                Delete for me
              </button>
              <button
                className={`_btn_delete _cancel bg-${theme} bb-${theme}`}
                onClick={handleDelete}
              >
                Cancel
              </button>
            </section>
          </div>
        </div>
      )}
      {!isMain ? (
        <>
          <div
            className={`_header_chat ${
              isMain ? "_main" : "_not_main"
            } fg-${theme}`}
          >
            {!isMain && (
              <Link to="/direct" className="_chat_back">
                <Icon icon="material-symbols:arrow-back-ios-new-rounded" />
              </Link>
            )}
            <Link to={`/${senderUser.nick}`}>
              <img src={senderUser.avatar} alt="" className="_avatar_chat" />
            </Link>
            <div className="_chat_sender_name">
              <h3>
                <Link
                  className={`c3-${invert(theme)}`}
                  to={`/${senderUser.nick}`}
                >
                  {senderUser.user}
                </Link>
              </h3>
              <span>
                <Link className={`c2-${theme}`} to={`/${senderUser.nick}`}>
                  {senderUser.nick}
                </Link>
              </span>
            </div>
            {selectedMessages.length > 0 && (
              <>
                <div className="_chat_header_icon _icon_favorite">
                  {/* <Icon icon="ic:baseline-star-outline" /> */}
                </div>
                <div
                  className={`_chat_header_icon _icon_delete c-${invert(
                    theme
                  )}`}
                  onClick={handleDelete}
                >
                  <Icon icon="ph:trash-simple-bold" />
                </div>
                <div
                  className="_chat_header_icon _icon_close"
                  onClick={closeHeaderMenu}
                >
                  <Icon icon="ph:x" />
                </div>
              </>
            )}
          </div>
          <div className="_chats_area" id="chats">
            <section className="_top_content_chat">
              <Link
                to={`/${nickLogged}`}
                className="_top_content_chat_image _own"
              >
                <img src={loggedUser.avatar} alt="" />
              </Link>
              <Link
                to={`/${senderUser.nick}`}
                className="_top_content_chat_image _sender"
              >
                <img src={senderUser.avatar} alt="" />
              </Link>
              <p>
                This is the beginning of your conversations with{" "}
                <Link to={`/${senderUser.nick}`}>{senderUser.user}</Link>. All
                your conversations are private and encrypted.
              </p>
            </section>
            {filterExcludeMessages(nickLogged, chats).map((chat, i) => (
              <Message
                chat={chat}
                chats={filterExcludeMessages(nickLogged, chats)}
                i={i}
                key={i}
                selectMessage={selectMessage}
                selectedMessages={selectedMessages}
              />
            ))}
          </div>
          <form className={`_comment fg-${theme}`} onSubmit={handleSendMessage}>
            <span>
              <Icon
                icon="ic:outline-emoji-emotions"
                onClick={openCloseEmojiPicker}
                className="_emoji_btn"
              />
              {emojiPicker && (
                <EmojiPicker theme={theme} onEmojiClick={addEmoji} />
              )}
            </span>
            <input
              className={`fg-${theme} c-${invert(theme)}`}
              type="text"
              placeholder="Type a message..."
              value={message}
              onChange={handleChange}
            />
            <button
              disabled={checkSend()}
              className="_send_comment_btn"
              type="submit"
            >
              <Icon icon="charm:paper-plane" />
            </button>
          </form>
        </>
      ) : (
        <section className="_send_a_direct">
          <div className="_send_a_direct_icon">
            <Icon icon="bx:paper-plane" />
          </div>
          <h4>Direct</h4>
          <span>Send private messages.</span>
        </section>
      )}
    </div>
  );
}

const mapStateToProps = (state) => ({
  nickLogged: state.user.nick,
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(DirectChat);
