import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { formatDateAndTime, invert } from "../../helpers";
import "./styles/Messages.css";

function Message({
  chats,
  chat,
  i,
  nickLogged,
  selectMessage,
  selectedMessages,
  theme
}) {
  const shouldRenderImgAvatar = (currentIndex) => {
    const indexToRender = [];
    const newChats = chats.map((chat, id) => {
      return {
        i: id,
        sender: chat.sender.nick,
      };
    });
    let sender = newChats[0].sender;
    newChats.forEach((chat, id) => {
      if (chat.sender !== sender) {
        indexToRender.push(id - 1);
        sender = chat.sender;
      }
    });
    indexToRender.push(chats.length - 1);
    return indexToRender.some((id) => id === currentIndex);
  };

  const getIdForSelected = () => {
    const idInSelected = selectedMessages.findIndex((currentMessage) => (
      currentMessage.id === chat.id
      && currentMessage.sender.nick === chat.sender.nick
      && currentMessage.recipient.nick === chat.recipient.nick
    ));
    return idInSelected + 1;
  };

  const selectedNumber = getIdForSelected();

  const { message, sender, recipient, date, id } = chat;
  const customClass = sender.nick === nickLogged ? `_own ballon-${theme}-own` : `_received ballon-${theme}-received`;
  const renderImg = shouldRenderImgAvatar(i);

  let timeoutId = "time";

  const holdMessage = () => {
    selectMessage(sender, recipient, id);
  };

  const dropMessage = () => {
    clearTimeout(timeoutId);
  };

  const isSelected = selectedMessages.some(
    (message) =>
      message.sender.nick === sender.nick &&
      message.recipient.nick === recipient.nick &&
      message.id === id
  );

  const howManySelected = selectedMessages.length;

  const haveSelected = howManySelected > 0;

  const notSelectedInSelected = haveSelected && !isSelected;

  return (
    <div
      key={i}
      className={`_message ${customClass}${isSelected ? " _selected" : ""}${
        notSelectedInSelected ? " _not_selected" : ""
      }`}
      onMouseDown={holdMessage}
      onMouseLeave={dropMessage}
    >
      {renderImg && (
        <Link
          to={`/${sender.nick}`}
          className={`_message_avatar ${customClass}`}
        >
          <img src={sender.avatar} alt="" />
        </Link>
      )}
      <p
        className={`_message_content ${!isSelected ? `c-${invert(theme)}` : 'c-dark'}`}
        onMouseDown={holdMessage}
        onMouseLeave={dropMessage}
      >
        {message}
      </p>
      <span className="_message_time">{formatDateAndTime(date).time}</span>
      {isSelected && (
        <span className="_number_selected">
          {selectedNumber}
        </span>
      )}
    </div>
  );
}
const mapStateToProps = (state) => ({
  nickLogged: state.user.nick,
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(Message);
