import lS from "manager-local-storage";
import { filterExcludeMessages, getElapsedMinutes, sortByTimerASC } from ".";
import { getUser } from "../mocks/fakeUsers";

//  MAIN FOR NAME OF USERS WITH CHATS

const usersWithChats = "biewwl-yourgram-users-with-chats";

// Get

const getUsersWithChats = () => lS.get(usersWithChats) ?? [];

// Set

const addUserWithChats = (user) => {
  const currentUsersWithChats = getUsersWithChats();
  const alreadyAddUser = currentUsersWithChats.some(
    (userName) => userName === user
  );
  if (!alreadyAddUser) {
    lS.set(usersWithChats, [...currentUsersWithChats, user]);
  }
};

// Remove

const removeUserWithChat = (user) => {
  const currentUsersWithChats = getUsersWithChats();
  const newUsersWithChats = currentUsersWithChats.filter(
    (userName) => userName !== user
  );
  if (newUsersWithChats.length > 0) {
    lS.set(usersWithChats, newUsersWithChats);
  } else {
    lS.remove(usersWithChats);
  }
};

// RECIPIENTS FOR SENDER

const recipientsForSender = (sender) =>
  `biewwl-yourgram-recipients-for-${sender}`;

// Get

export const getRecipientsForSender = (sender) =>
  lS.get(recipientsForSender(sender)) ?? [];

// Set

const addRecipientForSender = (sender, recipient) => {
  const currentRecipients = getRecipientsForSender(sender);
  const alreadyAddRecipient = currentRecipients.some(
    (recipientNick) => recipientNick === recipient
  );
  if (!alreadyAddRecipient) {
    lS.set(recipientsForSender(sender), [...currentRecipients, recipient]);
  }
};

// const addRecipientsForSender = (sender, recipients) => {
//   recipients.forEach((recipientName) => {
//     addRecipientForSender(sender, recipientName);
//   });
// };

// Remove

const removeRecipientForSender = (sender, recipient) => {
  const currentRecipients = getRecipientsForSender(sender);
  const newRecipients = currentRecipients.filter(
    (recipientName) => recipientName !== recipient
  );
  if (newRecipients.length > 0) {
    lS.set(recipientsForSender(sender), newRecipients);
  } else {
    lS.remove(recipientsForSender(sender));
    removeUserWithChat(sender);
  }
};

