// NUMBER OF MESSAGES BY USER IN CHAT

const numberOfMessagesByUserInChat = (
  sender,
  recipient
) => `biewwl-yourgram-SENDER::${sender}-RECIPIENT::${recipient}
`;

// Get

const getNumberOfMessagesInChatByUser = (sender, recipient) =>
  lS.get(numberOfMessagesByUserInChat(sender, recipient)) ?? 0;

// Set

const increaseNumberOfMessagesByUserInChat = (sender, recipient) => {
  const currentNumberOfMessages =
    getNumberOfMessagesInChatByUser(sender, recipient) ?? 0;
  const newNumberOfMessages = currentNumberOfMessages + 1;
  lS.set(numberOfMessagesByUserInChat(sender, recipient), newNumberOfMessages);
};

// Remove

const decreaseNumberOfMessagesByUserInChat = (sender, recipient) => {
  const currentNumberOfMessages =
    getNumberOfMessagesInChatByUser(sender, recipient) ?? 0;
  if (currentNumberOfMessages > 1) {
    const newNumberOfMessages = currentNumberOfMessages - 1;
    lS.set(
      numberOfMessagesByUserInChat(sender, recipient),
      newNumberOfMessages
    );
  } else {
    lS.remove(numberOfMessagesByUserInChat(sender, recipient));
  }
};

// Message

const messageInChat = (sender, recipient, messageId) =>
  `biewwl-yourgram-message-SENDER::${sender}-RECIPIENT::${recipient}-${messageId}`;

// Get

const getMessageFromSender = (sender, recipient, messageId) => {
  const message = lS.get(messageInChat(sender, recipient, messageId));
  if (message) {
    return { ...message, id: messageId };
  }
};

export const getAllMessagesFromSender = (sender, recipient) => {
  const numberOfMessages = getNumberOfMessagesInChatByUser(sender, recipient);
  const messages = [];
  for (let id = 0; id <= numberOfMessages; id += 1) {
    const message = getMessageFromSender(sender, recipient, id);
    if (message) messages.push(message);
  }
  return messages;
};

// Set

const addMessageFromSender = (sender, recipient, message, messageId) =>
  lS.set(messageInChat(sender, recipient, messageId), message);

// Remove

const removeMessageFromSender = (sender, recipient, messageId) =>
  lS.remove(messageInChat(sender, recipient, messageId));

