const createMessageSaveFormat = ({
  sender,
  recipient,
  message,
  date = new Date(),
  excludeBySender = false,
  excludeByRecipient = false,
}) => ({
  sender: getUser(sender),
  recipient: getUser(recipient),
  date,
  message,
  excludeBySender,
  excludeByRecipient,
});

const sortMessagesByElapsedMinutes = (messages) => {
  const sortedMessages = messages.map((message) => ({
    ...message,
    elapsedMinutes: getElapsedMinutes(message.date),
  }));
  return sortedMessages.sort(sortByTimerASC);
};

export const sendMessage = (sender, recipient, message) => {
  addUserWithChats(sender);
  addUserWithChats(recipient);
  addRecipientForSender(sender, recipient);
  addRecipientForSender(recipient, sender);
  const idFromMessage = getNumberOfMessagesInChatByUser(sender, recipient) + 1;
  increaseNumberOfMessagesByUserInChat(sender, recipient);
  addMessageFromSender(
    sender,
    recipient,
    createMessageSaveFormat({ sender, recipient, message }),
    idFromMessage
  );
};

