const excludeMessages = (sender, recipient, messages, excludeBy) => {
  const savedMessages = getAllMessagesFromSender(sender, recipient);
  const messageIds = messages.map((message) => message.id);
  const newSavedMessages = savedMessages.map((message, i) => {
    let newMessage = message;
    const messageId = i + 1;
    if (messageIds.includes(messageId)) {
      if (excludeBy === "sender" || excludeBy === "all") {
        newMessage.excludeBySender = true;
      }
      if (excludeBy === "recipient" || excludeBy === "all") {
        newMessage.excludeByRecipient = true;
      }
    }
    delete newMessage.id;
    return newMessage;
  });
  newSavedMessages.forEach((message, i) => {
    const messageId = i + 1;
    console.log(sender, recipient, message, messageId);
    addMessageFromSender(sender, recipient, message, messageId);
  });
  fixIdsForMessages(sender, recipient);
  fixIdsForMessages(recipient, sender);
};

export const excludeMessagesFromSender = (sender, recipient, messages) => {
  excludeMessages(sender, recipient, messages, "sender");
};

export const excludeMessagesFromEveryone = (sender, recipient, messages) => {
  excludeMessages(sender, recipient, messages, "all");
};

export const excludeMessagesFromRecipient = (sender, recipient, messages) => {
  excludeMessages(recipient, sender, messages, "recipient");
};

export const getAllMessagesInChat = (sender, recipient) => {
  const senderMessages = getAllMessagesFromSender(sender, recipient);
  const recipientMessages = getAllMessagesFromSender(recipient, sender);
  const allMessages = [...senderMessages, ...recipientMessages];
  const allMessagesReturn = filterExcludeMessages(sender, allMessages)
  return sortMessagesByElapsedMinutes(allMessagesReturn);
};

const getNumberOfMessagesInChat = (sender, recipient) =>
  getAllMessagesInChat(sender, recipient).length;

const deleteInSenders = (sender, recipient) => {
  const numberOfMessages = getNumberOfMessagesInChat(sender, recipient);
  if (numberOfMessages === 0) {
    removeRecipientForSender(sender, recipient);
  }
};

const fixIdsForMessages = (sender, recipient) => {
  const oldMessages = getAllMessagesFromSender(sender, recipient);
  const newMessages = oldMessages.filter((message) => {
    const { excludeBySender, excludeByRecipient } = message;
    return excludeBySender === false || excludeByRecipient === false;
  });
  const formattedNewMessages = newMessages.map((message) =>
    createMessageSaveFormat({ ...message, sender, recipient })
  );
  oldMessages.forEach((_message, i) => {
    const id = i + 1;
    removeMessageFromSender(sender, recipient, id);
    decreaseNumberOfMessagesByUserInChat(sender, recipient);
  });
  formattedNewMessages.forEach((message, i) => {
    const id = i + 1;
    addMessageFromSender(sender, recipient, message, id);
    increaseNumberOfMessagesByUserInChat(sender, recipient);
  });
  deleteInSenders(sender, recipient);
};
