import DirectChat from "../../components/DirectChat";
import DirectList from "../../components/DirectList";
import Header from "../../components/Header";
import { getRecipientsForSender } from "../../helpers/managerChats";
import { useState } from "react";
import { connect } from "react-redux";
import { getUser } from "../../mocks/fakeUsers";
import "./styles/Directs.css";
import "./styles/Directs-mobile.css";

function Directs({nickLogged, theme, isMain = false }) {
  const getRecipients = () => getRecipientsForSender(nickLogged);
  const getSendersChats = () =>
  getRecipients().map((recipient) => getUser(recipient));

  const [sendersChats, setSendersChats] = useState(getSendersChats());

  const updateSenders = () => {
    setSendersChats(getSendersChats());
  };

  return (
    <div className={`_page_direct bg-${theme}`}>
      <Header page="direct" />
      <main>
        <DirectList isMain={isMain} sendersChats={sendersChats} />
        <DirectChat isMain={isMain} updateSenders={updateSenders} />
      </main>
    </div>
  );
}

const mapStateToProps = (state) => ({
  nickLogged: state.user.nick,
  theme: state.theme.theme
});

export default connect(mapStateToProps)(Directs);
