import { Icon } from "@iconify/react";
import fakeTrending from "../../mocks/fakeTrending";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { invert } from "../../helpers";
import "./styles/Trending.css";
import "./styles/Trending-mobile.css";

function Trending({ theme }) {
  const { thumbnail, user, nick, subtitle, postDate, verified, views } =
    fakeTrending;

  return (
    <article className="_trending_content">
      <div className={`_trending_title c-${invert(theme)}`}>
        <Icon icon="el:fire" />
        <h2>Trending</h2>
      </div>
      <section className={`_trending_post bb-${theme}`}>
        <img
          src={thumbnail}
          alt="trending_post"
          className="_trending_thumbnail"
        />
        <section className="_trending_post_data">
          <h3 className={`_trending_post_subtitle c-${invert(theme)}`}>
            {subtitle}
          </h3>
          <Link
            className={`_trending_post_user c2-${invert(theme)}`}
            to={`/${nick}`}
          >
            <span>{user}</span>
            {verified && (
              <Icon
                icon="codicon:verified-filled"
                className="_verified_user_trending_post"
              />
            )}
          </Link>
          <span className={`_trending_post_date c2-${invert(theme)}`}>{postDate}</span>
          <span className={`_trending_post_views c-${invert(theme)}`}>
            <Icon icon="carbon:view" />
            <span>{views}</span>
          </span>
        </section>
      </section>
    </article>
  );
}

const mapStateToProps = (state) => ({
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(Trending);
