import lS from "manager-local-storage";

const currentTheme = lS.get("biewwl-website-theme") ?? 'light';

const initialState = {
  theme: currentTheme,
};

export const CHANGE_THEME = "CHANGE_THEME";

export default function theme(state = initialState, action) {
  switch (action.type) {
    case CHANGE_THEME:
      lS.set("biewwl-website-theme", action.payload);
      return {
        ...state,
        theme: action.payload,
      };

    default:
      break;
  }
  return state;
}