import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { getAllMessagesInChat } from "../../helpers/managerChats";
import { invert } from "../../helpers";
import "./styles/DirectList.css";
import "./styles/DirectList-mobile.css";

function DirectList({ nickLogged, isMain, sendersChats, theme }) {
  return (
    <aside
      className={`_direct_list${isMain ? " _main" : " _not_main"} bb-${theme}`}
    >
      <div className={`_direct_list_header  fg-${theme} c-${invert(theme)}`}>
        <h4>Chats</h4>
      </div>
      <ul>
        {sendersChats.map((sender, i) => {
          const { nick, avatar } = sender;

          const chat = getAllMessagesInChat(nickLogged, nick).pop();

          return (
            <li key={i} className="_direct_list_li">
              <Link to={`/${nick}`}>
                <img src={avatar} alt="" className="_direct_list_avatar" />
              </Link>
              <Link
                to={`/direct/${nick}`}
                className={`_direct_list_message_data c-${invert(theme)}`}
              >
                <span>{nick}</span>
                <div className="_direct_list_message">
                  <span>{chat.sender.nick === nickLogged ? "You" : nick}:</span>
                  <p>{chat.message}</p>
                </div>
              </Link>
            </li>
          );
        })}
      </ul>
    </aside>
  );
}

const mapStateToProps = (state) => ({
  nickLogged: state.user.nick,
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(DirectList);
