import { Navigate, Route, Routes } from "react-router-dom";
import Login from "./pages/Login";
import Home from "./pages/Home";
import Profile from "./pages/Profile";
import New from "./pages/New";
import EditProfile from "./pages/EditProfile";
import Follows from "./pages/Follows";
import Directs from "./pages/Directs";
import Post from "./pages/Post";
import { connect } from "react-redux";
import "./App.css";
import "./colors/colors.css";

function App({ status, nickLogged }) {
  return (
    <div className="App">
      <Routes>
        <Route exact path="/" element={status ? <Home /> : <Login />} />
        <Route
          exact
          path="/direct"
          element={status ? <Directs isMain /> : <Login />}
        />
        <Route
          exact
          path={`/direct/${nickLogged}`}
          element={status ? <Navigate to="/direct" /> : <Login />}
        />
        <Route
          exact
          path="/direct/:user"
          element={status ? <Directs /> : <Login />}
        />
        <Route exact path="/:nick" element={<Profile />} />
        <Route exact path="/:nick/:post" element={<Post />} />
        <Route
          exact
          path="/profile/edit"
          element={status ? <EditProfile /> : <Login />}
        />
        <Route
          exact
          path="/:nick/following"
          element={<Follows type="following" />}
        />
        <Route
          exact
          path="/:nick/followers"
          element={<Follows type="followers" />}
        />
        <Route exact path="/new" element={<New />} />
        <Route path="/" element={<h1>ok</h1>} />
      </Routes>
    </div>
  );
}

const mapStateToProps = (state) => ({
  status: state.user.status,
  nickLogged: state.user.nick,
});

export default connect(mapStateToProps)(App);
