import { Icon } from "@iconify/react";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
import { logoutUser } from "../../redux/actions/userAction";
import changeTheme from "../../redux/actions/themeAction";
import { invert } from "../../helpers";
import "./styles/HamburgerHeader.css";

function HamburgerHeader({ dispatch, theme }) {
  const nav = useNavigate();

  const editProfile = () => {
    nav(`/profile/edit`);
  };

  const logout = () => {
    dispatch(logoutUser());
    nav("/");
  };

  const alterTheme = () => {
    console.log("ok");
    if (theme === "light") {
      dispatch(changeTheme("dark"));
    } else {
      dispatch(changeTheme("light"));
    }
  };

  return (
    <aside className={`_menu_hamburger_header bg-${theme} bb-${theme}`}>
      <button onClick={editProfile} className={`c-${invert(theme)}`}>
        <Icon icon="bi:gear-wide-connected" />
        <span>Edit Profile</span>
      </button>
      <button onClick={alterTheme} className={`c-${invert(theme)}`}>
        {theme === "light" && <Icon icon="ph:moon-bold" />}
        {theme === "dark" && <Icon icon="ph:sun-bold" />}
        <span>{theme === "light" ? "Dark Theme" : "Light Theme"}</span>
      </button>
      <button onClick={logout} className={`_logout_button c-${invert(theme)}`}>
        <Icon icon="heroicons-outline:logout" />
        <span>Logout</span>
      </button>
    </aside>
  );
}

const mapStateToProps = (state) => ({
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(HamburgerHeader);
