import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  getFollowingByNick,
  getFollowsByNick,
} from "../../mocks/fakeFollows";
import { connect } from "react-redux";
import "./styles/Follows.css";
import "./styles/Follows-mobile.css";
import Follow from "../../components/Follow";
import Profile from "../Profile";
import { Link } from "react-router-dom";
import { Icon } from "@iconify/react";
import Header from "../../components/Header";
import { invert, sortFollows } from "../../helpers";

function Follows({ type, nickLogged, theme }) {
  const [follows, setFollows] = useState([]);

  const { nick } = useParams();

  useEffect(() => {
    const setCurrentFollows = () => {
      if (type === "following") {
        const currentFollows = getFollowingByNick(nick) ?? [];
        setFollows(currentFollows);
      }
      if (type === "followers") {
        const currentFollows = getFollowsByNick(nick) ?? [];
        setFollows(currentFollows);
      }
    };
    setCurrentFollows();
  }, [nick, type]);

  const reloadFollows = () => {
    if (type === "following") {
      const currentFollows = getFollowingByNick(nick) ?? [];
      setFollows(currentFollows);
    }
    if (type === "followers") {
      const currentFollows = getFollowsByNick(nick) ?? [];
      setFollows(currentFollows);
    }
  };

  return (
    <div className="_follows_page_content">
      <Header />
      <Profile />
      <div className={`_follows_page ${theme}`}>
        <main className={`_follows fg-${theme}`}>
          <section className={`_header_follows bg-${theme} bbB-${theme}`}>
            <h4 className={`c-${invert(theme)}`}>
              {type === "following" ? "Following" : "Followers"} ({" "}
              {follows.length} )
            </h4>
            <Link to={`/${nick}`} className="_quit_follows">
              <Icon icon="bi:x" />
            </Link>
          </section>
          {sortFollows(follows, nickLogged).map((followData, i) => (
            <Follow
              followData={followData}
              key={i}
              reloadFollows={reloadFollows}
              type={type}
            />
          ))}
        </main>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  nickLogged: state.user.nick,
  theme: state.theme.theme,
});

export default connect(mapStateToProps)(Follows);
